.about-box{
    background:url(../../static/media/dong.13a7e620.jpg) no-repeat;
    background-size:cover;
    background-attachment:fixed;
    padding:30px 20px 30px;
    color:#000;
}
.user-info{
    width:100%;
    overflow:hidden;
    margin-bottom:30px;
    padding:10px;
    display: flex;
    border:1px solid #ededed;
}
.about-img{
    width:240px;
    height: auto;
    min-height:200px;
}
.about-img img{
    width:100%;
    height:auto;
}
.info-txt{
    flex: 1 1;
    padding-left:20px;
    overflow:hidden;
}
.info-txt .info-item{
    width:100%;
    overflow: hidden;
    line-height:32px;
}
.info-txt .info-item .t{
    display: inline-block;
    width:100px;
}
.info-txt .info-item .v{
    color:#333;
}
.express{
    width: 100%;
    padding:10px 20px 20px;
    border:1px solid #ededed;
}
.express .expre-list{
    padding-left: 10px;
}
.express .tit{
    width:100%;
    line-height:30px;
    font-size:16px;
    margin-bottom:10px;
}
.express .item{
    width: 100%;
    overflow: hidden;
    padding:10px 0;
    border-bottom:1px solid #ededed;
}
.express .item .time-spre,.express .item .item-tit{
    width: 100%;
    overflow: hidden;
    font-size:14px;
    line-height: 30px;
}
.express .item .time-spre{
    font-weight:normal;
}
.express .item .item-desc{
    width: 100%;
    text-indent:2em;
    overflow: hidden;
    line-height:26px;
    color:#333;
}
